const routers = [
    {
        path: '/',
        meta: {
            title: '首页'
        },
        component: (resolve) => require(['./views/index.vue'], resolve)
    },
    {
        path: '/result',
        meta: {
            title: '结果'
        },
        component: (resolve) => require(['./views/result.vue'], resolve)
    },
    {
        path: '/test',
        meta: {
            title: '结果'
        },
        component: (resolve) => require(['./views/test.vue'], resolve)
    }
];
export default routers;


